    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
}

.nav-presentation {
    display: inline-flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
}

.nav-presentation-title {
    display: inline-flex;
    flex-direction: column;
    justify-content: center;
    width: 50%;
    color: #f3f5f5;
    box-sizing: border-box;
}

.nav-presentation > img {
    display: block;
    line-height: 0;
    aspect-ratio: 1 / 1;
    box-sizing: border-box;
    border-radius: 10px;
}

.nav-presentation-title > h1 {
    margin: 0;
    line-height: 120%;
}

.nav-presentation-title h1 > #cyan {
    color: #3eb9e2;
}

.nav-presentation-title > h4 {
    margin: 0;
    font-weight: 600;
}

.nav-presentation-title > p {
    margin: 0;
    font-weight: 500;
}

.nav-btn-section {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    width: auto;
    height: 100%;
    box-sizing: border-box;
}

.nav-list {
    display: inline-flex;
    padding: 0;
    margin: 0;
    justify-content: center;
    align-items: center;
}

.nav-item {
    display: inline-flex;
    flex-shrink: 1;
    justify-content: center;
    align-items: center;
    border: 1px solid #f6f6f69b;
    width: max-content;
    height: fit-content;
    min-width: 30%;
    border-radius: 2vw;
    list-style-type: none;
    transition: background-color 0.2s ease, transform 0.2s ease;
    background-color: #5a6163ef;
    text-wrap: nowrap;
    box-sizing: border-box;
}

.nav-link {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: bold;
    height: auto;
    width: 100%;
    padding: 0.5vw 2vw;
    text-decoration: none;
    line-height: 180%;
    color: #3eb9e2;
    border-radius: 8px;
    box-sizing: border-box;
    background: transparent;
    transition: color 0.5s ease;
}

.nav-item:hover {
    background-color: #5a6163a4;
    transform: scale(1.1);
    overflow: hidden;
    box-sizing: border-box;
    border: 1px solid #ece6e65f;
}

.nav-item:hover .nav-link {
    color: #d8d5d5fb
}

.nav-item:active {
    background-color: #4d85b0;
    transform: scale(1);
}

