    display: inline-flex;
    flex-direction: column;
}

.trajectory-subcontainer {
    display: flex;
    flex-direction: column;
}

.trajectory-title {
    margin: 0;
    color: #606060;
    position: relative;
}

.trajectory-main {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    width: 100%;
    box-sizing: border-box;
}

/**************** accordiones *****************/
.accordion-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.accordion-label-container {
    display: inline-flex;
    flex-direction: row;
    position: relative;
}

.accordion-label-container > label {
    display: inline-block;
    text-wrap: nowrap;
    width: 100%;
    box-sizing: border-box;
    font-weight: 700;
    color: aliceblue;
    background: linear-gradient(150deg, #253164 0%, #0070ef80 85%, #c3cdd8 92%, #0070ef80 100%);
}

.accordion-label-container > button {
    display: inline-flex;
    justify-content: flex-end;
    align-items: center;
    width: 100%;
    height: 100%;
    border: 0;
    padding: 1%;
    background: transparent;
    box-sizing: border-box;
    position: absolute;
    z-index: 1;
    cursor: pointer;
}

.accordion-label-container > button > .arrow-dropdown {
    display: inline-flex;
    flex-direction: row;
    align-items: center;
    height: 100%;
}


.accordion-label-container > button > .arrow-dropdown > div {
    display: inline-block;
    background-color: #f7f4f4;
    width: 50%;
    border-radius: 5px;
    transition: all 0.5s;
    transform-origin: center;
}

.left {transform: rotate(-40deg) translateX(18%) translateY(18%);}
.right {transform: rotate(40deg) translateX(-18%) translateY(18%);}

.active-left {transform: rotate(40deg) translateX(18%) translateY(-18%);}
.active-right {transform: rotate(-40deg) translateX(-18%) translateY(-18%);}


.accordion-content {
    display: inline-block;
    overflow: hidden;
    transition: height 0.8s ease;
    position: relative;
    width: 100%;
    box-sizing: border-box;
}

.accordion-content > div > span {
    display: inline-block;
}

.accordion-content > div > ul {
    margin: 0
}

@media screen and (max-width: 719px) {
    .trajectory-container {
        padding: 8% 5% 5.5% 5%;
        gap: 9.5vw;
    }
    .trajectory-subcontainer {
        gap: 8vw;
    }
    .trajectory-title {
        font-size: calc(16px + 1vw);
        text-align: center;
        right: 0;
    }
    .trajectory-main {
        flex-direction: column;
        gap: 5vw;
    }
    
    /**** accordiones ****/
    .accordion-container {
        display: flex;
        flex-direction: column;
        justify-content: center;
        gap: 4vw;
        width: calc((100% - (5vw * (1 - 1))) / 1);
        
    }
    .accordion-label-container > label {
        font-size: calc(9px + 1vw);
        padding: 1vw 1.5vw;
    }
    .accordion-label-container > button > .arrow-dropdown > div {
        height: 0.8vw;
        width: calc(7px + 1vw);
    }
    .accordion-content {
        padding: 0 4vw;
    }
    .accordion-content > div > span {
        font-size: calc(9px + 1vw);
        margin-bottom: 2%;
    }
    
    .accordion-content > div > ul {
        font-size: calc(9px + 1vw);
        padding-left: 6vw
    }
}

@media screen and (min-width: 720px) and (max-width: 1024px) {
    .trajectory-container {
        padding: 5% 5% 3.5% 5%;
        gap: 3vw;
    }
    .trajectory-subcontainer {
        gap: 2.3vw;
    }
    .trajectory-title {
        font-size: calc(15px + 0.5vw);
        text-align: left;
        right: 0.8%;
    }

    .trajectory-main {
        flex-direction: row;
        gap: 3.5vw;
    }
    /**** accordiones ****/
    .accordion-container {
        gap: 1.5vw;
        width: calc((100% - (3.5vw * (2 - 1))) / 2);
        
    }
    .accordion-label-container > label {
        font-size: calc(9px + 0.5vw);
        padding: 0.2vw 0.4vw;
    }
    .accordion-label-container > button > .arrow-dropdown > div {
        height: 0.5vh;
        width: calc(7px + 0.5vw);
    }
    .accordion-content {
        padding: 0 4vw;
    }
    .accordion-content > div > span {
        font-size: calc(9px + 0.5vw);
        margin-bottom: 1.5%;
    }
    .accordion-content > div > ul {
        font-size: calc(8px + 0.5vw);
        padding-left: 2vw;
        height: max-content
    }
}

@media screen and (min-width: 1025px) {
    .trajectory-container {
        padding: 5% 5% 3.5% 5%;
        gap: 3vw;
    }
    .trajectory-subcontainer {
        gap: 2.3vw;
    }
    .trajectory-title {
        font-size: calc(15px + 0.5vw);
        text-align: left;
        right: 0.8%;
    }

    .trajectory-main {
        flex-direction: row;
        gap: 3.5vw;
    }
    /**** accordiones ****/
    .accordion-container {
        gap: 1.5vw;
        width: calc((100% - (3.5vw * (2 - 1))) / 2);
        
    }
    .accordion-label-container > label {
        font-size: calc(9px + 0.5vw);
        padding: 0.2vw 0.4vw;
    }
    .accordion-label-container > button > .arrow-dropdown > div {
        height: 0.5vh;
        width: calc(7px + 0.5vw);
    }
    .accordion-content {
        padding: 0 4vw;
    }
    .accordion-content > div > span {
        font-size: calc(9px + 0.5vw);
        margin-bottom: 1.5%;
    }
    .accordion-content > div > ul {
        font-size: calc(8px + 0.5vw);
        padding-left: 2vw;
        height: max-content
    }
}

