___CSS_LOADER_EXPORT___.push([module.id, `.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
    line-height: normal;
}

.modal-content {
    background: white;
    padding: 1%;
    border-radius: 5px;
    position: relative;
    width: 50%;
    max-width: fit-content;
    max-height: 90%;
}

.close {
    display: inline-block;
    position: absolute;
    top: 1%;
    right: 1%;
    background: none;
    border: none;
    cursor: pointer;
    line-height: normal;
    padding: 0;
    font-weight: 600;
    color:#3f3f3f;
    border-radius: 15px;
    font-family: Arial, sans-serif;
    -webkit-text-stroke: 1px white;
}

.close:hover {
    transform: scale(0.9);
}

.certs-modal-img {
    display: block;
    width: auto;
    height: auto;
    max-width: 100%;
    max-height: 100%;
}

@media screen and (max-width: 719px) {
    .modal-content {
        transform: scale(1.8)
    }

    .close {
        font-size: calc(30px + 1vw);
        width: calc(30px + 1vw);
        height: calc(30px + 1vw);
    }
}

@media screen and (min-width: 720px) and (max-width: 1024px) {
    .modal-content {
        transform: scale(1.7)
    }
    .close {
        font-size: calc(34px + 0.5vw);
        width: calc(38px + 0.5vw);
        height: calc(38px + 0.5vw);
    }
}

@media screen and (min-width: 1025px) {
    .modal-content {
        transform: scale(1.5)
    }
    .close {
        font-size: calc(38px + 0.5vw);
        width: calc(38px + 0.5vw);
        height: calc(38px + 0.5vw);
    }
