___CSS_LOADER_EXPORT___.push([module.id, `.certs-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.certs-title {
    width: 100%;
    margin: 0;
    color: #606060;
    position: relative;
}

.certs-section {
    display: inline-flex;
    flex-direction: row;
    padding: 0;
    margin: 0;
    justify-content: center;
    width: 100%;
}

.certs-card {
    display: inline-flex;
    padding: 0;
    margin: 0;
    border: 1px solid #7a7a7a;
    flex-grow: 1;
    flex-shrink: 1;
    transition: all .5s ease;
    box-sizing: border-box;
}

.certs-card > button {
    display: inline-flex;
    padding: 0;
    margin: 0;
    border: none;
    background: none;
    width: 100%;
    height: auto;
    overflow: hidden;
}

.certs-card > button > img {
    display: block;
    object-fit: cover;
    line-height: 0;
    height: 115%;
    width: 115%;
    cursor: pointer;
    box-sizing: border-box;
    filter: brightness(0.95);
    transition: filter .5s ease;
}

.certs-card:hover {
    width: 10%;
    border: 1px solid #c7c7c777;
}
.certs-card:hover > button > img {
    filter: brightness(0.98);
}

@media screen and (max-width: 719px) {
    .certs-container {
        padding: 8% 5% 5.5% 5%;
        gap: 8vw;
    }
    
    .certs-title {
        font-size: calc(16px + 1vw);
        text-align: center;
        right: 0;
    }
    
    .certs-section {
        flex-wrap: wrap;
        gap: 3vw;
    }
    
    .certs-card {
        flex-basis: calc((100% - (3vw * (2 - 1))) / 2);
        max-width: calc((100% - (3vw * (2 - 1))) / 2);
        height: 100px;
    }
}

@media screen and (min-width: 720px) and (max-width: 1024px) {
    .certs-container {
        padding: 5% 5.5% 3.5% 5%;
        gap: 2.3vw;
    }

    .certs-title {
        font-size: calc(15px + 0.5vw);
        text-align: left;
        right: 0.8%;
    }

    .certs-section {
        flex-wrap: nowrap;
    }

    .certs-card {
        width: 0;
        height: 300px;
    }
}

@media screen and (min-width: 1025px) {
    .certs-container {
        padding: 5% 5.5% 3.5% 5%;
        gap: 2.3vw;
    }

    .certs-title {
        font-size: calc(15px + 0.5vw);
        text-align: left;
        right: 0.8%;
    }

    .certs-section {
        flex-wrap: nowrap;
    }

    .certs-card {
        width: 0;
        height: 300px;
    }
}`, "",{"version":3,"sources":["webpack://./src/assets/styles/info/certs.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,WAAW;IACX,SAAS;IACT,cAAc;IACd,kBAAkB;AACtB;;AAEA;IACI,oBAAoB;IACpB,mBAAmB;IACnB,UAAU;IACV,SAAS;IACT,uBAAuB;IACvB,WAAW;AACf;;AAEA;IACI,oBAAoB;IACpB,UAAU;IACV,SAAS;IACT,yBAAyB;IACzB,YAAY;IACZ,cAAc;IACd,wBAAwB;IACxB,sBAAsB;AAC1B;;AAEA;IACI,oBAAoB;IACpB,UAAU;IACV,SAAS;IACT,YAAY;IACZ,gBAAgB;IAChB,WAAW;IACX,YAAY;IACZ,gBAAgB;AACpB;;AAEA;IACI,cAAc;IACd,iBAAiB;IACjB,cAAc;IACd,YAAY;IACZ,WAAW;IACX,eAAe;IACf,sBAAsB;IACtB,wBAAwB;IACxB,2BAA2B;AAC/B;;AAEA;IACI,UAAU;IACV,2BAA2B;AAC/B;AACA;IACI,wBAAwB;AAC5B;;AAEA;IACI;QACI,sBAAsB;QACtB,QAAQ;IACZ;;IAEA;QACI,2BAA2B;QAC3B,kBAAkB;QAClB,QAAQ;IACZ;;IAEA;QACI,eAAe;QACf,QAAQ;IACZ;;IAEA;QACI,8CAA8C;QAC9C,6CAA6C;QAC7C,aAAa;IACjB;AACJ;;AAEA;IACI;QACI,wBAAwB;QACxB,UAAU;IACd;;IAEA;QACI,6BAA6B;QAC7B,gBAAgB;QAChB,WAAW;IACf;;IAEA;QACI,iBAAiB;IACrB;;IAEA;QACI,QAAQ;QACR,aAAa;IACjB;AACJ;;AAEA;IACI;QACI,wBAAwB;QACxB,UAAU;IACd;;IAEA;QACI,6BAA6B;QAC7B,gBAAgB;QAChB,WAAW;IACf;;IAEA;QACI,iBAAiB;IACrB;;IAEA;QACI,QAAQ;QACR,aAAa;IACjB;AACJ","sourcesContent":[".certs-container {\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: center;\r\n    align-items: center;\r\n}\r\n\r\n.certs-title {\r\n    width: 100%;\r\n    margin: 0;\r\n    color: #606060;\r\n    position: relative;\r\n}\r\n\r\n.certs-section {\r\n    display: inline-flex;\r\n    flex-direction: row;\r\n    padding: 0;\r\n    margin: 0;\r\n    justify-content: center;\r\n    width: 100%;\r\n}\r\n\r\n.certs-card {\r\n    display: inline-flex;\r\n    padding: 0;\r\n    margin: 0;\r\n    border: 1px solid #7a7a7a;\r\n    flex-grow: 1;\r\n    flex-shrink: 1;\r\n    transition: all .5s ease;\r\n    box-sizing: border-box;\r\n}\r\n\r\n.certs-card > button {\r\n    display: inline-flex;\r\n    padding: 0;\r\n    margin: 0;\r\n    border: none;\r\n    background: none;\r\n    width: 100%;\r\n    height: auto;\r\n 