___CSS_LOADER_EXPORT___.push([module.id, `.comps-container {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
}

/********************** Competencies ***********************/
.comps-subcontainer {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: auto;
}
.comps-title {
    margin: 0;
    padding: 0;
    color: #606060;
    position: relative;
}
.comps-main {
    display: inline-flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
}
.comps-flipcard-container {
    display: inline-block;
    background-color: transparent;
    flex-grow: 1;
    perspective: 1000px;
}
.comps-flipcard-inner {
    display: inline-flex;
    flex-direction: column;
    position: relative;
    width: 100%;
    height: 100%;
    transition: transform 1.8s;
    transform-style: preserve-3d;
}

.comps-flipcard-front {
    display: inline-flex;
    flex-direction: column;
    gap: 0.5em;
    border: 1px solid #c0b9b9;
    border-radius: 5px;
    box-sizing: border-box;
    position: relative;
    cursor: pointer;
    background: url("https://i.ibb.co/PmKdLBh/abstract-white-squares.jpg");
    background-size: 120%;
}
.comps-progressbar-title {
    max-width: 100%;
    padding: 0;
    margin: 0;
    color: #404040;
    font-weight: 700;
}
.comps-progressbar-container {
    display: block;
    border: 1px solid #000000;
    background-color: #f1f1f1;
    position: relative;
    border-radius: 2px;
    width: 100%;
    height: auto;
}
.comps-count {
    display: inline-block;
    position: absolute;
    font-size: 1.1em;
    left: 44%;
    line-height: 138%;
    background-color: transparent;
    overflow: hidden;
    font-weight: 700;
    color: #2c2c2c;
    max-height: 100%;
    z-index: 0;
}
.comps-bar {
    display: block;
    width: 0%;
    border-right: 1px solid #000000;
    z-index: -1;
    background: linear-gradient(135deg,
    #64abd1 30%, #6eb8d5 20%, 
    #6eb8d5 50%, #64abd1 50%, 
    #64abd1 80%, #6eb8d5 70%, 
    #6eb8d5 100%);
    background-size: 2.5em 2.5em;
    animation: loading 12s infinite linear;
}

@keyframes loading {
    from {
        background-position: 0 0;
    }
    to {
        background-position: 2.5em 2.5em; /* Debe coincidir con el tamaño del patrón */
    }
}
.comps-flipcard-back {
    background-color: #333;
    color: white;
    transform: rotateY(180deg);
    display: inline-block;
    border-radius: 5px;
    border: 1px solid #9a9a9a;
    line-height: 0;
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    position: absolute;
    top: 0;       
    right: 0;        
    bottom: 0;  
    left: 0;
    max-height: 100%;
    cursor: pointer;
}
.comps-flipcard-back img {
    display: inline-block;
    width: 100%;
    height: 100%;
    object-fit: fill;
    line-height: 0;
    padding: 0;
    margin: 0;
    border-radius: 4px;
    max-height: 100%;
    box-sizing: border-box;
}
.comps-flipcard-front,.comps-flipcard-back {
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
    -webkit-backface-visibility: hidden; /* Safari */
}

@media screen and (max-width: 719px) {
    .comps-container {
        padding: 8% 5% 5.5% 5%;
        gap: 9.5vw;
    }
    .comps-subcontainer {
        gap: 8vw;
    }
    /*********** Comps ***********/
    .comps-title {
        font-size: calc(16px + 1vw);
        text-align: center;
        right: 0;
    }
    .comps-main {
        gap: 3vw;
    }
    .comps-flipcard-container {
        flex-basis: calc((100% - (3vw * (2 - 1))) / 2);
        max-width: calc((100% - (3vw * (2 - 1))) / 2);
    }
    .comps-flipcard-front {
        padding: 3.5vw;
    }
    .comps-progressbar-title {
        font-size: calc(10px + 1vw);
    }
    .comps-count {
        font-size: calc(8px + 1vw);
    }
    .comps-bar {
        height: calc(10px + 1vw);
    }
}

@media screen and (min-width: 720px) and (max-width: 1024px) {
    .comps-container {
        padding: 5% 5% 3.5% 5%;
        gap: 3.8vw;
    }
    .comps-subcontainer {
        gap: 2.5vw;
    }
    /*********** Comps ***********/
    .comps-title {
        font-size: calc(15px + 0.5vw);
        text-align: left;
        right: 0.8%;
    }
    .comps-main {
        gap: 1.5vw;
    }
    .comps-flipcard-container {
        flex-basis: calc((100% - (1.5vw * (4 - 1))) / 4);
        max-width: calc((100% - (1.5vw * (4 - 1))) / 4);
    }
    .comps-flipcard-front {
        padding: 1.6vw;
    }
    .comps-progressbar-title {
        font-size: calc(6px + 0.75vw);
    }
    .comps-count {
        font-size: calc(4px + 0.75vw);
    }
    .comps-bar {
        height: calc(6px + 0.75vw);
    }
}

@media screen and (min-width: 1025px) {
    .comps-container {
        padding: 5% 5% 3.5% 5%;
        gap: 3.8vw;
    }
    .comps-subcontainer {
        gap: 2.5vw;
    }
    /*********** Comps ***********/
    .comps-title {
        font-size: calc(15px + 0.5vw);
        text-align: left;
        right: 0.8%;
    }
    .comps-main {
        gap: 1.5vw;
    }
    .comps-flipcard-container {
        flex-basis: calc((100% - (1.5vw * (5 - 1))) / 5);
        max-width: calc((100% - (1.5vw * (5 - 1))) / 5);
    }
    .comps-flipcard-front {
        padding: 1.6vw;
    }
    .comps-progressbar-title {
        font-size: calc(6px + 0.5vw);
    }
    .comps-count {
        font-size: calc(4px + 0.5vw);
    }
    .comps-bar {
        height: calc(7px + 0.5vw);
    }
}`, "",{"version":3,"sources":["webpack://./src/assets/styles/info/comps.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,sBAAsB;IACtB,WAAW;IACX,YAAY;AAChB;;AAEA,4DAA4D;AAC5D;IACI,aAAa;IACb,sBAAsB;IACtB,WAAW;IACX,YAAY;AAChB;AACA;IACI,SAAS;IACT,UAAU;IACV,cAAc;IACd,kBAAkB;AACtB;AACA;IACI,oBAAoB;IACpB,mBAAmB;IACnB,eAAe;IACf,WAAW;AACf;AACA;IACI,qBAAqB;IACrB,6BAA6B;IAC7B,YAAY;IACZ,mBAAmB;AACvB;AACA;IACI,oBAAoB;IACpB,sBAAsB;IACtB,kBAAkB;IAClB,WAAW;IACX,YAAY;IACZ,0BAA0B;IAC1B,4BAA4B;AAChC;;AAEA;IACI,oBAAoB;IACpB,sBAAsB;IACtB,UAAU;IACV,yBAAyB;IACzB,kBAAkB;IAClB,sBAAsB;IACtB,kBAAkB;IAClB,eAAe;IACf,sEAAsE;IACtE,qBAAqB;AACzB;AACA;IACI,eAAe;IACf,UAAU;IACV,SAAS;IACT,cAAc;IACd,gBAAgB;AACpB;AACA;IACI,cAAc;IACd,yBAAyB;IACzB,yBAAyB;IACzB,kBAAkB;IAClB,kBAAkB;IAClB,WAAW;IACX,YAAY;AAChB;AACA;IACI,qBAAqB;IACrB,kBAAkB;IAClB,gBAAgB;IAChB,SAAS;IACT,iBAAiB;IACjB,6BAA6B;IAC7B,gBAAgB;IAChB,gBAAgB;IAChB,cAAc;IACd,gBAAgB;IAChB,UAAU;AACd;AACA;IACI,cAAc;IACd,SAAS;IACT,+BAA+B;IAC/B,WAAW;IACX;;;;iBAIa;IACb,4BAA4B;IAC5B,sCAAsC;AAC1C;;AAEA;IACI;QACI,wBAAwB;IAC5B;IACA;QACI,gCAAgC,EAAE,4CAA4C;IAClF;AACJ;AACA;IACI,sBAAsB;IACtB,YAAY;IACZ,0BAA0B;IAC1B,qBAAqB;IACrB,kBAAkB;IAClB,yBAAyB;IACzB,cAAc;IACd,UAAU;IACV,SAAS;IACT,sBAAsB;IACtB,kBAAkB;IAClB,MAAM;IACN,QAAQ;IACR,SAAS;IACT,OAAO;IACP,gBAAgB;IAChB,eAAe;AACnB;AACA;IACI,qBAAqB;IACrB,WAAW;IACX,YAAY;IACZ,gBAAgB;IAChB,cAAc;IACd,UAAU;IACV,SAAS;IACT,kBAAkB;IAClB,gBAAgB;IAChB,sBAAsB;AAC1B;AACA;IACI,WAAW;IACX,YAAY;IACZ,2BAA2B;IAC3B,mCAAmC,EAAE,WAAW;AACpD;;AAEA;IACI;QACI,sBAAsB;QACtB,UAAU;IACd;IACA;QACI,QAAQ;IACZ;IACA,8BAA8B;IAC9B;QACI,2BAA2B;QAC3B,kBAAkB;QAClB,QAAQ;IACZ;IACA;QACI,QAAQ;IACZ;IACA;QACI,8CAA8C;QAC9C,6CAA6C;IACjD;IACA;QACI,cAAc;IAClB;IACA;QACI,2BAA2B;IAC/B;IACA;QACI,0BAA0B;IAC9B;IACA;QACI,wBAAwB;IAC5B;AACJ;;AAEA;IACI;QACI,sBAAsB;QACtB,UAAU;IACd;IACA;QACI,UAAU;IACd;IACA,8BAA8B;IAC9B;QACI,6BAA6B;QAC7B,gBAAgB;QAChB,WAAW;IACf;IACA;QACI,UAAU;IACd;IACA;QACI,gDAAgD;QAChD,+CAA+C;IACnD;IACA;QACI,cAAc;IAClB;IACA;QACI,6BAA6B;IACjC;IACA;QACI,6BAA6B;IACjC;IACA;QACI,