___CSS_LOADER_EXPORT___.push([module.id, `footer {
    display: inline-flex;
    flex-direction: column;
    border: 1px solid #ababab;
    height: 100%;
    min-height: 2.5em;
    padding: 2.5% 3.5% 0.5% 3.5%;
    box-sizing: border-box;
    background: linear-gradient(135deg, 
      #0077b5 0%, 
      #1e668d 20%, 
      #4da1c3 40%, 
      #0077b5 60%, 
      #1e668d 75%, 
      #1e668d 85%,  
      #0077b5 100%
    );
}

.foot-element-container {
    display: inline-flex;
    align-items: center
}

.foot-contactinfo-container {
    display: flex;
    justify-content: flex-start;
    width: auto;
    margin-left: 0.8%;
}

.foot-contactinfo-list {
    padding: 0;
    margin: 0;
    display: block;
}

.foot-contactinfo-item {
    list-style: none;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    width: auto;
    display: flex;
}

.foot-contactinfo-item > img {
    display: inline-block;
    line-height: 0;
    object-fit: contain;
    width: 16px;
    height: 16px;
    margin-right: 0.2em;
}

.foot-contactinfo-item > span {
    display: inline;
    width: auto;
    line-height: normal;
    color: #dde1e3;
}

.foot-contactinfo-item > a {
    display: inline;
    width: auto;
    line-height: normal;
    text-decoration: none;
    color: #c0dbea;
}

.foot-contactinfo-item > a:hover {
    color: #dde1e3;
}

.foot-contactinfo-item > a:hover {
    text-decoration: underline;
}

/*********************** date ************************/

.foot-date-container {
    display: inline-flex;
    width: 100%;
    flex-direction: column;
    align-items: center;
}

.foot-date-container hr {
    display: inline;
    width: 100%;
    height: 0.1px; 
    background-color: #9e9e9e;
    border: none;
}

.foot-date-text {
    font-size: 13px;
    color: #dde1e3
}

@media screen and (max-width: 719px) {
    footer {
        border-radius: 0;
        width: 100%;
        margin: 0;
        box-shadow: 2px 2px 7px #00000080;
    }

    .foot-contactinfo-item > img {
        width: calc(7px + 1vw);
        height: calc(7px + 1vw);
    }
    
    .foot-contactinfo-item > span {
        font-size: calc(7px + 1vw);
    }

    .foot-contactinfo-item > a {
        font-size: calc(7px + 1vw);
    }

    /**** date ****/
    .foot-date-text {
        font-size: calc(4px + 1vw);;
    }

    .foot-date-container hr {
        margin: 1.5% 0% 0.5% 0%;
    }
}

@media screen and (min-width: 720px) and (max-width: 1024px) {
    footer {
        border-radius: 5px;
        width: 99%;
        margin-right: 0.5%;
        margin-left: 0.5%;
        margin-bottom: 0.5%;
        box-shadow: 3px 3px 8px #00000080;
    }

    .foot-contactinfo-item > img {
        width: calc(7px + 0.5vw);
        height: calc(7px + 0.5vw);
    }
    
    .foot-contactinfo-item > span {
        font-size: calc(7px + 0.5vw);
    }

    .foot-contactinfo-item > a {
        font-size: calc(7px + 0.5vw);
    }

    /**** date ****/
    .foot-date-text {
        font-size: calc(4px + 0.5vw);
    }

    .foot-date-container hr {
        margin: 1% 0% 0.2% 0%;
    }
}

