___CSS_LOADER_EXPORT___.push([module.id, `.layout-container {
    min-height: 90vh;
    display: inline-flex;
    flex-direction: column;
    justify-content: space-between;
    box-sizing: border-box;
}

.layout-container > div {
    position: relative;
    box-sizing: border-box;
    border-radius: 5px;
    background: #e7e9eb;
}

@media screen and (max-width: 719px) {
    .layout-container {
        width: 92%;
        margin: 5.5% 4%;
        gap: 25px;
    }

    .layout-container > div {
        border: 2px solid #c8c8c8;
        box-shadow: 2px 2px 7px #00000080;
    }
}

@media screen and (min-width: 720px) and (max-width: 1024px) {
    .layout-container {
        width: 94%;
        margin: 3.5% 3%;
        gap: 3vh;
    }

    .layout-container > div {
        border: 2px solid #c8c8c8;
        box-shadow: 2px 2px 7px #00000080;
    } 
}

@media screen and (min-width: 1025px) {
    .layout-container {
        width: 94%;
        margin: 3.5% 3%;
        gap: 5vh;
    }

    .layout-container > div {
        border: 3px solid #c8c8c8;
        box-shadow: 3px 3px 8px #00000080;
    }
}`, "",{"version":3,"sources":["webpack://./src/assets/styles/layout.css"],"names":[],"mappings":"AAAA;IACI,gBAAgB;IAChB,oBAAoB;IACpB,sBAAsB;IACtB,8BAA8B;IAC9B,sBAAsB;AAC1B;;AAEA;IACI,kBAAkB;IAClB,sBAAsB;IACtB,kBAAkB;IAClB,mBAAmB;AACvB;;AAEA;IACI;QACI,UAAU;QACV,eAAe;QACf,SAAS;IACb;;IAEA;QACI,yBAAyB;QACzB,iCAAiC;IACrC;AACJ;;AAEA;IACI;QACI,UAAU;QACV,eAAe;QACf,QAAQ;IACZ;;IAEA;QACI,yBAAyB;QACzB,iCAAiC;IACrC;AACJ;;AAEA;IACI;QACI,UAAU;QACV,eAAe;QACf,QAAQ;IACZ;;IAEA;QACI,yBAAyB;QACzB,iCAAiC;IACrC;AACJ","sourcesContent":[".layout-container {\r\n    min-height: 90vh;\r\n    display: inline-flex;\r\n    flex-direction: column;\r\n    justify-content: space-between;\r\n    box-sizing: border-box;\r\n}\r\n\r\n.layout-container > div {\r\n    position: relative;\r\n    box-sizing: border-box;\r\n    border-radius: 5px;\r\n    background: #e7e9eb;\r\n}\r\n\r\n@media screen and (max-width: 719px) {\r\n    .layout-container {\r\n        width: 92%;\r\n        margin: 5.5% 4%;\r\n        gap: 25px;\r\n    }\r\n\r\n    .layout-container > div {\r\n        border: 2px solid #c8c8c8;\r\n        box-shadow: 2px 2px 7px #00000080;\r\n    }\r\n}\r\n\r\n@media screen and (min-width: 720px) and (max-width: 1024px) {\r\n    .layout-container {\r\n        width: 94%;\r\n        margin: 3.5% 3%;\r\n        gap: 3vh;\r\n    }\r\n\r\n    .layout-container > div {\r\n        border: 2px solid #c8c8c8;\r\n        box-shadow: 2px 2px 7px #00000080;\r\n    } \r\n}\r\n\r\n@media screen and (min-width: 1025px) {\r\n    .layout-container {\r\n        width: 94%;\r\n        margin: 3.5% 3%;\r\n        gap: 5vh;\r\n    }\r\n\r\n    .layout-container > div {\r\n        border: 3px solid #c8c8c8;\r\n        box-shadow: 3px 3px 8px #00000080;\r\n    }\r\n}"],"sourceRoot":""}]);
